import React, {useState, useEffect} from 'react';
import {connect} from 'react-redux';
import HeroComponents from "components/layout-components/CustomrPageHeaderAlt";
import ButtonPermission from "components/permission-components/button.permission";
import ContainerTailwind from "components/layout-components/ContainerTailwind";
import {Button, Card, Col, Divider, Form, Input, Menu, message, Modal, Row, Table, Tooltip} from "antd";
import {
	DeleteOutlined,
	EditOutlined,
	EyeOutlined,
	FilterOutlined,
	PlusCircleOutlined,
	ReloadOutlined
} from "@ant-design/icons";
import {Link, useHistory} from 'react-router-dom'
import ApiService from "services/api.service";
import EllipsisDropdown from "components/shared-components/EllipsisDropdown";
import Flex from "components/shared-components/Flex";
import {GetPath} from "utils";
import LayoutPermission from "components/permission-components/layout.permission";
import ImageUploadsForm from "components/shared-components/uploads/image-uploads.form";


function mapStateToProps(state) {
	return {};
}

function Index() {
	const history = useHistory()
	const [loading, setLoading] = useState(true)
	const [params, setParams] = useState({
		page: 1,
		limit: 10
	})
	const [pagination, setPagination] = useState({
		page: 1,
		limit: 10,
		total_items: 0
	})
	const [data, setData] = useState([])
	
	
	const [loadings, setLoadings] = useState([])
	
	
	const [visible, setVisible] = useState(false)
	const [selected, setSelected] = useState(null)
	const [form] = Form.useForm();
	
	function _caller() {
		setLoading(true)
		setData([])
		new ApiService({
			url: "/api/v1/master-data/location",
      config : {
        params: {
          ...params
        }
      }
		})
			.get()
			.then((response) => {
				setPagination({...pagination, ...params, ...response?.pagination, ...response?.paginate})
				setData([...response?.data])
				setLoading(false)
			})
			.catch((err) => {
				setData([])
				setLoading(false)
			})
	}
	
	
	useEffect(() => {
		_caller()
	}, [params])
	
	useEffect(() => {
		form.resetFields()
	}, [visible])
	
	
	const enterLoading = (index, value = true) => {
		setLoadings((prevLoadings) => {
			prevLoadings[index] = value;
			return prevLoadings ?? []
		});
	};
	
	const onFinish = () => {
		form.validateFields()
			.then((value) => {
				new ApiService({
					body: value,
					url: `/api/v1/master-data/city`
				}).post()
					.then((response) => {
						message.success({
							content: response?.message ?? "Successfully created",
							key: "create-city",
							duration: 2
						})
						setLoading(false)
					})
					.catch((err) => {
						message.info(JSON.stringify(err))
						message.error({
							content: err?.message ?? "Error: Some Error",
							key: "create-city",
							duration: 2
						})
						setLoading(false)
					})
			})
			.catch((err) => {
			
			})
	}
	
	function handleClickEdit(_, index) {
		form.resetFields()
		setVisible(!visible)
		setSelected(_)
		
	}
	
	function onCloseModal() {
		form.resetFields()
		setVisible(false)
		setSelected(null)
		
	}
	
	
	function handleDeleteRow(_, index) {
		enterLoading(index, true)
		new ApiService({
			url: `/api/v1/master-data/location/${_}`,
		}).delete()
			.then((response) => {
				if (response?.success) {
					if (data && typeof (data[index]) !== 'undefined') {
						data[index].isDeleted = true
						setData([...data])
						enterLoading(index, false)
					}
					message.success(response?.message ?? "Successfully deleted")
					setTimeout(() => {
						setData([...data.filter((item) => item.id !== _)])
					}, 2000)
				} else {
					enterLoading(index, false)
					message.error(response?.message ?? "Error: delete by id " + _)
				}
			})
			.catch((err) => {
				enterLoading(index, false)
			})
	}
	
	const columns = [
		{
			dataIndex: "id",
			width: 70,
			render: (_, val, index) => ((index + 1) + (params?.limit * (params?.page - 1)))
		},
		// {
		// 	key: "identifier-location",
		// 	title: "Identifier",
		// 	dataIndex: "identifier"
		// },
		{
			dataIndex: ['data', 'name'],
			title: "Display Name",
			render: (_) => _ ?? "-"
		},
		{
			dataIndex: ['data', 'code'],
			title: "Code Location",
			render: (_) => _ ?? "-"
		},
		{
			key: 'action',
			width: 120,
			dataIndex: 'id',
			render: (_, val, index) => {
				return (
					<div className={'text-right d-flex !mk-gap-2'}>
						<Button size={'small'} type={'primary'} ghost icon={<EditOutlined/>}
						        onClick={() => handleClickEdit(val, index)}/>
						<Button
							size={'small'}
							type={'primary'}
							danger
							ghost
							onClick={() => {
								handleDeleteRow(_, index)
							}}
							loading={loadings[index] ?? false}
							icon={<DeleteOutlined/>}/>
					</div>
				)
			}
		},
	]
	return (
		<React.Fragment>
			
			
			<Modal
				style={{
					width: "520px !important",
				}}
				title={'Edit'}
				visible={visible} onOk={onFinish} onCancel={onCloseModal}>
				<Form form={form} initialValues={{...selected,}} layout={'vertical'}>
					<Form.Item name={'identifier'} hidden label={'Identifier'}
					           rules={[{required: true, message: "Can't be empty!"}]}>
						<Input/>
					</Form.Item>
					<Form.Item name={['data', 'code']} label={'Code'} rules={[{required: true, message: "Can't be empty!"}]}>
						<Input/>
					</Form.Item>
					<Form.Item name={['data', 'name']} label={'Display Name'}
					           rules={[{required: true, message: "Can't be empty!"}]}>
						<Input/>
					</Form.Item>
				</Form>
			</Modal>
			
			
			<HeroComponents
				loading={false}
				name={"Overview"}
				positionName={['Master Data', 'Airlines', 'Status']}
				thumbnail={null}
				extra={<></>}
			/>
			
			<div style={{marginTop: 90}}>
				<ContainerTailwind>
					<Card bordered={false}
					      extra={<Button icon={<ReloadOutlined/>} ghost type={'primary'} onClick={_caller} loading={loading}/>}>
						<Table
							loading={loading}
							dataSource={data}
							rowClassName={(record, index) => record?.isDeleted ? "!mk-bg-red-500 !mk-bg-opacity-20" : ''}
							columns={columns}
							pagination={{
								pageSize: params?.limit,
								current: params?.page,
								total: pagination?.total_items ?? 0,
								onChange: function (page, pageSize) {
									setParams({
										...params,
										page,
										limit: pageSize
									})
								}
							}}
						/>
					</Card>
				</ContainerTailwind>
			</div>
		</React.Fragment>
	);
}

export default connect(
	mapStateToProps,
)(Index);